@import "shared";

#contentContainer {
	position: relative;
	z-index: 2;
	background-color: $article-background-color;
	font-family: Arial;
	font-size: 10pt;
    padding: 10px;
    color: black;
    width: 750px;
    box-sizing: border-box;
}

@media only screen and (max-width: $mobile-breakpoint) {
	#contentContainer {
	    width: 100%;
	}
}

@media only screen and (max-width: $one-column-breakpoint) {
	#contentContainer {
	    /* Use flex to place text before right rail. */
	    display: flex;
	    flex-direction: column;
	}
	#textContainer {
	    order: 1;
	}
	#rightRail {
		order: 2;
		width: 100%;
		margin: 0;
		box-sizing: border-box;
	}
}
